.loading{
  position:relative;
  width:100%;
  height:100%;
  top:0;
  left:0;
  background:black
}

.loading div:nth-child(1){
  position:absolute;
  z-index:50;
  -webkit-transform:translateX(-50%) translateY(-50%);
  transform:translateX(-50%) translateY(-50%);
  top:50%;
  left:50%;
  color:black
}

.blob{
  position:absolute;
  width:500px;
  height:500px;
  top:50%;
  left:50%;
  margin:-250px;
  background:black;
  -webkit-filter:contrast(15);
  filter:contrast(15)
}

.blob span{
  background:white;
  position:absolute;
  border-radius:50%;
  display:inline-block;
  -webkit-filter:blur(10px);
  filter:blur(10px)
}

.blob p{
  position:absolute;
  top:50%;
  left:50%
}

.blob p:nth-child(1){
  position:absolute;
  -webkit-animation:skewing-child .2s ease-in-out infinite alternate;
  animation:skewing-child .2s ease-in-out infinite alternate
}

.blob p:nth-child(1) span{
  width:50px;
  height:50px;
  margin:-25px;
  -webkit-animation:moving 2s cubic-bezier(0.97, 0.01, 0.12, 0.99) infinite alternate;
  animation:moving 2s cubic-bezier(0.97, 0.01, 0.12, 0.99) infinite alternate
}

.blob p:nth-child(2){
  position:absolute;
  -webkit-animation:squishing 1s ease-in-out infinite alternate;
  animation:squishing 1s ease-in-out infinite alternate
}

.blob p:nth-child(2) span{
  width:120px;
  height:120px;
  top:50%;
  left:50%;
  margin:-60px;
  -webkit-animation:skewing 3s 1.5s ease-in-out infinite;
  animation:skewing 3s 1.5s ease-in-out infinite
}

@-webkit-keyframes skewing{
  0%{
    -webkit-transform:skewX(6deg);
    transform:skewX(6deg)
  }

  10%{
    -webkit-transform:skewX(-6deg);
    transform:skewX(-6deg)
  }

  20%{
    -webkit-transform:skewX(4deg);
    transform:skewX(4deg)
  }

  40%{
    -webkit-transform:skewX(2deg);
    transform:skewX(2deg)
  }

  50%{
    -webkit-transform:skewX(-6deg);
    transform:skewX(-6deg)
  }

  60%{
    -webkit-transform:skewX(-5deg);
    transform:skewX(-5deg)
  }

  65%{
    -webkit-transform:skewX(5deg);
    transform:skewX(5deg)
  }

  75%{
    -webkit-transform:skewX(4deg);
    transform:skewX(4deg)
  }

  85%{
    -webkit-transform:skewX(3deg);
    transform:skewX(3deg)
  }

  90%{
    -webkit-transform:skewX(-2deg);
    transform:skewX(-2deg)
  }

  95%{
    -webkit-transform:skewX(2deg);
    transform:skewX(2deg)
  }

  100%{
    -webkit-transform:skewX(1deg);
    transform:skewX(1deg)
  }
}

@keyframes skewing{
  0%{
    -webkit-transform:skewX(6deg);
    transform:skewX(6deg)
  }

  10%{
    -webkit-transform:skewX(-6deg);
    transform:skewX(-6deg)
  }

  20%{
    -webkit-transform:skewX(4deg);
    transform:skewX(4deg)
  }

  40%{
    -webkit-transform:skewX(2deg);
    transform:skewX(2deg)
  }

  50%{
    -webkit-transform:skewX(-6deg);
    transform:skewX(-6deg)
  }

  60%{
    -webkit-transform:skewX(-5deg);
    transform:skewX(-5deg)
  }

  65%{
    -webkit-transform:skewX(5deg);
    transform:skewX(5deg)
  }

  75%{
    -webkit-transform:skewX(4deg);
    transform:skewX(4deg)
  }

  85%{
    -webkit-transform:skewX(3deg);
    transform:skewX(3deg)
  }

  90%{
    -webkit-transform:skewX(-2deg);
    transform:skewX(-2deg)
  }

  95%{
    -webkit-transform:skewX(2deg);
    transform:skewX(2deg)
  }

  100%{
    -webkit-transform:skewX(1deg);
    transform:skewX(1deg)
  }
}

@-webkit-keyframes skewing-child{
  0%{
    -webkit-transform:skewX(-10deg);
    transform:skewX(-10deg)
  }

  100%{
    -webkit-transform:skewX(10deg);
    transform:skewX(10deg)
  }
}

@keyframes skewing-child{
  0%{
    -webkit-transform:skewX(-10deg);
    transform:skewX(-10deg)
  }

  100%{
    -webkit-transform:skewX(10deg);
    transform:skewX(10deg)
  }
}

@-webkit-keyframes moving{
  0%{
    -webkit-transform:translate(-200px);
    transform:translate(-200px)
  }

  30%{
    -webkit-transform:translate(-45px);
    transform:translate(-45px)
  }

  70%{
    -webkit-transform:translate(45px);
    transform:translate(45px)
  }

  100%{
    -webkit-transform:translate(200px);
    transform:translate(200px)
  }
}

@keyframes moving{
  0%{
    -webkit-transform:translate(-200px);
    transform:translate(-200px)
  }

  30%{
    -webkit-transform:translate(-45px);
    transform:translate(-45px)
  }

  70%{
    -webkit-transform:translate(45px);
    transform:translate(45px)
  }

  100%{
    -webkit-transform:translate(200px);
    transform:translate(200px)
  }
}

@-webkit-keyframes squishing{
  10%,40%,80%{
    -webkit-transform:scale(1, 0.9);
    transform:scale(1, 0.9)
  }

  0%,30%,60%,100%{
    -webkit-transform:scale(0.9, 1);
    transform:scale(0.9, 1)
  }
}

@keyframes squishing{
  10%,40%,80%{
    -webkit-transform:scale(1, 0.9);
    transform:scale(1, 0.9)
  }

  0%,30%,60%,100%{
    -webkit-transform:scale(0.9, 1);
    transform:scale(0.9, 1)
  }
}

