@media screen and (min-width: 53em){
  .frame{
    display:grid;
    height:100vh;
    padding:2rem 3.7rem;
    pointer-events:none;
    -webkit-align-content:space-between;
            align-content:space-between;
    grid-template-columns:50% 50%;
    grid-template-rows:auto auto auto;
    grid-template-areas:"title links" "... ..." "... nav"
  }

  .frame__title{
    grid-area:title
  }

  .frame__links{
    grid-area:links;
    justify-self:end;
    margin:0
  }

  .frame__link{
    pointer-events:auto
  }

  .frame__nav{
    display:block;
    grid-area:nav;
    justify-self:end
  }

  .bottom-left{
    padding:0 0 2rem 3.7rem;
    width:30vw
  }
}

